// Global css

.axl-wp-ultimate {

  .admin-errors {
    color:$dark-red;
  }

  // global admin css
  padding:20px;

  h1{
    padding:20px;
  }



  .nav-tab {
    border-radius:$border-radius;
    margin:0px;
    border-bottom:none;
    padding:5px 20px;

    &.nav-tab-active {
      background-color:$white;
    }
  }

  .content-wrapper {
    background-color:$white;
    padding:20px;
    border:none;
  }

  .admin-header {
    color:$green;
    //font-weight:bold;
    margin-bottom:20px;
  }


  // member module css

  #axl_wp_ultimate_member, #axl_wp_ultimate_contact_form, #axl_wp_ultimate_menu_cart {
    
    .settings {

      input[type='radio'] {
        margin-left:15px;
      }

      textarea {
        width:100%;
      }
    }
  }


   // member module css

  #axl_wp_ultimate_woopdf {
    
    .settings {

      input[type='text'] {
        width:50%;
      }

    }
  }




  // Custom Post Types CSS
  #axl_wp_ultimate_cpt {

    .list {

      form { 
        display:inline-block;

        #submit{ 
          display:inline-block;
          margin:5px;
        }
      }
      
      table{

        width: 100%;
        border: 1px gray solid;
        text-align: center; ;
        border-collapse: collapse;
        background-color:$white !important;

        th, td {
          border: 1px gray solid;
          text-align: center;
          padding:10px;
        }

      }

    }  // END  &.list {

  } // END  .axl_wp_ultimate_cpt {




  // Member CSS 
  #axl_wp_ultimate_member {

    .notify_who, .from_email_name, .user_registration_body, .admin_registration_body, .approved_registration_body, .password_reset_body {
      padding-bottom:50px;
      border-bottom: $border;
    }


  }  // END  .axl_wp_ultimate_member {




  // Menu Cart CSS 
  #axl_wp_ultimate_menu_cart {



  }  // END  .axl_wp_ultimate_menu_cart {


  



} // END .axl-wp-ultimate {


// Woocommerce

.widefat .column-wc_actions { 

  a.axl-wp-ultimate-packing-slip {
    &:after {
      content: '\f495';
    }
  }

  a.axl-wp-ultimate-invoice {
    &:after {
      content: '\f491';
    }
  }


}






